const SafeEventEmitter=require("@metamask/safe-event-emitter").default;let BaseFilter=class extends SafeEventEmitter{async initialize(){}async update(){throw new Error("BaseFilter - no update method specified")}addResults(newResults){this.updates=this.updates.concat(newResults),newResults.forEach((result=>this.emit("update",result)))}addInitialResults(newResults){}getChangesAndClear(){const updates=this.updates;return this.updates=[],updates}constructor(){super(),this.updates=[]}};module.exports=BaseFilter;